import { Component, Event, EventEmitter, h, Host, Prop } from '@stencil/core';
import { generateUid, parseClassNames, resolvePropertyValue } from '../../../utils/utils';
import type { ToolbarItemInterface } from '../interface';

@Component({
  tag: 'storm-toolbar-item',
  styleUrl: 'storm-toolbar-item.css',
  shadow: false,
})
export class StormToolbarItem {
  @Prop({ mutable: true }) uid: string;
  @Prop() dataSet: ToolbarItemInterface;

  @Event() toolbarAction: EventEmitter<string>;

  private _active?: boolean;
  private _disabled?: boolean;

  handleToolbarAction(e: Event) {
    e.stopPropagation();
    this.toolbarAction.emit(this.dataSet.id);
  }

  initDataSet() {
    if (this.dataSet.id) {
      this.uid = this.dataSet.id;
    } else if (!this.uid) {
      this.dataSet.id = this.uid = generateUid('storm-toolbar-item');
    }
  }

  componentWillLoad() {
    this.initDataSet();
  }

  async componentWillRender() {
    const promises: Promise<any>[] = [];
    
    if (this.dataSet.active) {
      promises.push(this.resolveActive().then(result => this._active = result));
    }
    if (this.dataSet.disabled) {
      promises.push(this.resolveDisabled().then(result => this._disabled = result));
    }
    
    if (promises.length > 0) {
      await Promise.all(promises);
    }
  }

  private async resolveActive(): Promise<boolean> {
    const result = resolvePropertyValue(this.dataSet, 'active');
    return result instanceof Promise ? await result : result;
  }

  private async resolveDisabled(): Promise<boolean> {
    const result = resolvePropertyValue(this.dataSet, 'disabled');
    return result instanceof Promise ? await result : result;
  }

  render() {
    return (
      <Host class={parseClassNames(this.dataSet.class)}>
        <storm-button
          type={this.dataSet.buttonType}
          label={this.dataSet.label}
          tooltip={this.dataSet.tooltip}
          icon={this.dataSet.icon}
          buttonStyle={this.dataSet.buttonStyle}
          size={this.dataSet.size}
          active={this._active}
          disabled={this._disabled}
          onButtonClick={event => this.handleToolbarAction(event)}
          rounded={this.dataSet.rounded}
        ></storm-button>
      </Host>
    );
  }
}
