import type { Middleware, Placement } from '@floating-ui/dom';
import { autoUpdate, computePosition, flip, offset, shift, size } from '@floating-ui/dom';
import { Component, Element, Event, EventEmitter, h, Host, Listen, Method, Prop, State, Watch } from '@stencil/core';
import { FnIcon, FnTag } from '../../utils/functional/functional';
import type { ListItemInterface } from '../../utils/interface';
import { appendHighlight, arrayEqual, generateUid, getSelectedItemIds, isNonEmptyValue, mergeClasses, parseClassNames } from '../../utils/utils';

@Component({
  tag: 'storm-dropdown',
  styleUrl: 'storm-dropdown.css',
  shadow: false,
})
export class StormDropdown {
  private _dropdownRef: HTMLElement;
  private _dropdownToggleRef: HTMLElement;
  private _dropdownMenuRef: HTMLElement;
  private _cleanupAutoUpdate: () => void;
  private _listGroupRef: HTMLStormListGroupElement;
  private _dropdownClasses: Record<string, boolean> | string = 'dropdown';
  private _toggleClasses: Record<string, boolean> | string = 'storm-dropdown-toggle';

  @Element() host: HTMLElement;

  @Prop({ mutable: true }) heading: string;
  @Prop({ mutable: true }) label: string;
  @Prop({ mutable: true }) icon: string;
  @Prop({ mutable: true }) text?: string;
  @Prop({ mutable: true }) value?: string | string[];
  @Prop({ mutable: true }) dataSet: ListItemInterface[];
  @Prop({ mutable: true }) disabled?: boolean;
  @Prop() showSelected: boolean = true;

  @Prop({ mutable: true }) searchText?: string;
  @Prop() searchable: boolean = false;

  @Prop({ mutable: true }) valid?: boolean;
  @Prop() required?: boolean;

  @Prop() buttonStyle?: 'primary' | 'secondary' | 'success' | 'info' | 'warning' | 'danger' | 'link' | 'dark';
  @Prop() selection: 'single' | 'multiple' | 'none' = 'single';
  @Prop() trigger: 'click' | 'hover' = 'click';
  @Prop() toggleIcon: string = 'expand_more';
  @Prop() placement: Placement = 'bottom-start';
  @Prop() type: 'btn' | 'select' | 'none' = 'btn';
  @Prop() class: Record<string, boolean> | string;
  @Prop() dropdownClass: Record<string, boolean> | string;
  @Prop() toggleClass?: Record<string, boolean> | string;
  @Prop() headingClass?: Record<string, boolean> | string;
  @Prop() nullable: boolean = false;
  @Prop() autoHideMenu: boolean = true;
  @Prop({ mutable: true }) timeStamp?: number;

  @State() isFocused: boolean = false;
  @State() isValid?: boolean;

  @Event() valueChanged: EventEmitter<string | string[]>;
  @Event() itemClick: EventEmitter<ListItemInterface>;
  @Event() selectChange: EventEmitter<ListItemInterface>;
  @Event() unselectedAll: EventEmitter;
  @Event() showMenu: EventEmitter;
  @Event() hideMenu: EventEmitter;
  @Event() keydown: EventEmitter<KeyboardEvent>;

  @Watch('value')
  valueWatcher(newValue: string | string[], oldValue: string | string[]) {
    if (arrayEqual(newValue, oldValue)) return;

    this.setSelectedItems();

    this.valueChanged.emit(newValue);
    this.validate();
  }

  @Watch('dataSet')
  dataSetWatcher() {
    this.initDataSet();
    this.setSelectedItems();

    if (this.showSelected) {
      this.text = undefined;
    }
  }

  @Listen('itemClick')
  async itemClickHandler(event: CustomEvent<ListItemInterface>) {
    if (this.selection != 'none') {
      const detail = event.detail;
      if (detail.selected) {
        this.unselect(detail);
      } else {
        this.select(detail);
      }
      this.selectChange.emit(detail);
    }
    if (this.autoHideMenu) await this.hide();
  }

  @Listen('showMenu')
  async showMenuHandler(_: CustomEvent) {
    this.isFocused = true;
    this.updatePosition();
    if (this._listGroupRef) {
      requestAnimationFrame(async () => {
        await this._listGroupRef.setSearchFocus();
      });
    }
  }

  @Listen('hideMenu')
  hideMenuHandler(_: CustomEvent) {
    this.isFocused = false;
  }

  @Listen('searchChanged')
  searchChangedHandler(ev: CustomEvent<string>) {
    this.searchText = ev.detail;
  }

  @Listen('keydown')
  keydownHandler(ev: KeyboardEvent) {
    if (!this.dataSet || this.dataSet.length === 0) return;
    switch (ev.key) {
      case 'ArrowDown':
        this.select(this.getNextItem());
        break;
      case 'ArrowUp':
        this.select(this.getPrevItem());
        break;
    }
  }

  @Method()
  async show() {
    this.showMenu.emit();
  }

  @Method()
  async hide() {
    this.hideMenu.emit();
  }

  @Method()
  async selectById(values: string[]) {
    for (const x of this.dataSet.filter(x => values.includes(x.id))) {
      this.select(x);
    }
  }

  @Method()
  async unselectAll() {
    this.value = null;
    this.text = undefined;
    this.unselectedAll.emit();
  }

  select(item: ListItemInterface) {
    if (item.selected) return;
    item.selected = true;
    switch (this.selection) {
      case 'single':
        this.value = item.id;
        break;
      case 'multiple': {
        const currentValue = Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];
        if (!currentValue.includes(item.id)) this.value = [...currentValue, item.id];
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
  }

  unselect(item: ListItemInterface) {
    if (!item.selected) return;
    item.selected = false;
    switch (this.selection) {
      case 'single':
        if (this.value === item.id) {
          this.value = null;
        }
        break;
      case 'multiple': {
        const currentValue = Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];
        if (currentValue.includes(item.id)) this.value = currentValue.filter(id => id !== item.id);
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
  }

  initDataSet() {
    if (!Array.isArray(this.dataSet)) return;
    this.dataSet.forEach(item => {
      item.id = item.id || generateUid('ilist-item');
    });
  }

  setSelectedItems() {
    if (!Array.isArray(this.dataSet)) return;
    const valueArray = Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];
    const valueSet = new Set<string>(valueArray);
    this.dataSet.forEach(item => {
      item.selected = valueSet.has(item.id);
    });
  }

  validate(): void {
    if (this.valid !== undefined || this.required !== true) return;
    this.isValid = isNonEmptyValue(this.value);
  }

  getNextItem() {
    const index = this.dataSet.findIndex(x => x.selected);
    return this.dataSet[index + 1] ?? this.dataSet[0];
  }

  getPrevItem() {
    const index = this.dataSet.findIndex(x => x.selected);
    return this.dataSet[index - 1] ?? this.dataSet[this.dataSet.length - 1];
  }

  renderDropdownHeading() {
    return (
      <div
        class={mergeClasses(
          {
            'storm-dropdown-input-group': true,
            'align-middle': true,
            'me-auto': true,
            'nav-link-text': true,
            'text-truncat': true,
            'm-1': this.type !== 'select',
            'px-0': this.type === 'select',
          },
          this.headingClass,
        )}
      >
        {this.renderSelectedText()}
        {this.renderHeadingText()}
      </div>
    );
  }

  renderSelectedText() {
    if (this.text != null && this.text != '') {
      return <span class="inline-block">{this.text}</span>;
    } else if (this.showSelected && this.dataSet) {
      if (this.selection === 'single' || this.type === 'btn') {
        return (
          <span class="inline-block">
            {this.dataSet
              .filter(x => x.selected)
              .map(x => x.title)
              .join('')}
          </span>
        );
      }
      return this.dataSet.filter(x => x.selected).map(x => <FnTag lable={x.title} onClickCancel={_ => this.unselect(x)}></FnTag>);
    }
  }

  renderHeadingText() {
    if (this.heading) {
      return <span class="inline-block">{this.heading}</span>;
    }
  }

  renderHeadingIcon() {
    if (this.showSelected && this.dataSet) {
      const selectedItems = this.dataSet.filter(x => x.selected && x.icon);
      const selectedIcon = selectedItems.length ? selectedItems[0].icon : this.icon;
      return <FnIcon class={parseClassNames(this.headingClass)}>{selectedIcon}</FnIcon>;
    }
    return <FnIcon class={parseClassNames(this.headingClass)}>{this.icon}</FnIcon>;
  }

  renderMenus() {
    if (!this.dataSet) return <slot></slot>;

    return (
      <storm-list-group
        ref={elm => (this._listGroupRef = elm)}
        dataSet={this.dataSet}
        value={this.value}
        searchable={this.searchable}
        selection={this.selection}
        nullable={this.nullable}
        itemClass="border-0 rounded-0"
        onValueChanged={ev => {
          ev.stopPropagation();
          this.value = ev.detail;
        }}
        onItemClick={async ev => {
          ev.stopPropagation();
          if (this.autoHideMenu) await this.hide();
        }}
      ></storm-list-group>
    );
  }

  renderItemText(title: string) {
    return <span class={{ 'nav-link-text': true }} innerHTML={!this.searchText || !title ? title : appendHighlight(title, this.searchText)}></span>;
  }

  renderLabel() {
    if (!this.label) return;
    return (
      <div class="storm-label d-flex">
        <label>{this.label}</label>
      </div>
    );
  }

  renderClearButton() {
    if (this.disabled === true) return;
    if (!this.nullable || this.value == null || this.value === '' || this.selection === 'none' || (Array.isArray(this.value) && this.value.length === 0)) return;
    return (
      <div
        role="button"
        tabindex="0"
        aria-label="Clear Button"
        class={{
          'dropdown-clearable': true,
          'p-2': true,
        }}
        onClick={async ev => {
          ev.stopPropagation();
          await this.unselectAll();
        }}
      >
        <FnIcon>cancel</FnIcon>
      </div>
    );
  }

  componentWillLoad() {
    this.initDataSet();
    if (!this.value || (Array.isArray(this.value) && this.value.length === 0)) {
      this.value = getSelectedItemIds(this.dataSet, this.nullable, this.selection);
    }
    this.setSelectedItems();
    this.validate();
  }

  componentWillRender() {
    this._dropdownClasses = mergeClasses(this.dropdownClass, this._dropdownClasses);
    this._toggleClasses = mergeClasses(this.toggleClass, this._toggleClasses);

    if (this.type !== 'none') {
      if (!this.dropdownClass) {
        this._dropdownClasses['d-flex'] = true;
        if (this.type === 'select') {
          this._dropdownClasses['dropdown-select'] = true;
          this._toggleClasses['w-100'] = true;
          this._toggleClasses['p-0'] = true;
        }
      }
      if (!this.toggleClass) {
        this._toggleClasses['d-flex'] = true;
        this._toggleClasses['justify-content-end'] = true;
        this._toggleClasses['align-items-center'] = true;
        if (this.type === 'btn') {
          this._toggleClasses['btn'] = true;
          this._toggleClasses['bg-gradient-' + (this.buttonStyle ?? 'primary')] = true;
        }
      }
    }
    this._dropdownClasses['disabled'] = this.disabled === true;
  }

  render() {
    return (
      <Host tabindex="0">
        <div
          class={mergeClasses(this.class, {
            'align-middle': true,
            disabled: this.disabled === true,
            'is-focused': this.isFocused,
            'storm-dropdown-select': this.type === 'select',
            'storm-dropdown-multiple': this.selection === 'multiple',
            'storm-is-valid': this.disabled !== true && (this.valid === true || this.isValid === true),
            'storm-is-invalid': (this.disabled !== true && this.valid === false) || this.isValid === false,
          })}
        >
          {this.renderLabel()}
          <div class={this._dropdownClasses} ref={elm => (this._dropdownRef = elm)}>
            <a
              href="javascript:void(0);"
              class={this._toggleClasses}
              ref={elm => {
                this._dropdownToggleRef = elm;
              }}
              aria-describedby="tooltip"
            >
              {this.renderHeadingIcon()}
              {this.renderDropdownHeading()}
              {this.renderClearButton()}
              {this.disabled !== true ? <FnIcon class="toggle">{this.toggleIcon}</FnIcon> : null}
            </a>
            <div role="tooltip" tabindex="-1" class={{ 'storm-dropdown-menu': true, 'p-1': true }} ref={elm => (this._dropdownMenuRef = elm)}>
              {this.renderMenus()}
            </div>
          </div>
        </div>
      </Host>
    );
  }

  componentDidRender() {
    if (this.disabled === true) return;
    if (this._cleanupAutoUpdate) {
      this._cleanupAutoUpdate();
      this._cleanupAutoUpdate = undefined;
    }
    if (this._dropdownToggleRef && this._dropdownMenuRef) {
      this._cleanupAutoUpdate = autoUpdate(this._dropdownToggleRef, this._dropdownMenuRef, () => this.updatePosition());
    }

    if (this.trigger === 'hover') {
      const showEvents = ['mouseenter', 'focus'];
      showEvents.forEach(type => {
        this.host.removeEventListener(type, this.handleShow);
        this.host.addEventListener(type, this.handleShow);
      });
      const hideEvents = ['mouseleave', 'focusout'];
      hideEvents.forEach(type => {
        this.host.removeEventListener(type, this.handleHide);
        this.host.addEventListener(type, this.handleHide);
      });
    } else {
      this._dropdownToggleRef.removeEventListener('click', this.handleClick);
      this._dropdownToggleRef.addEventListener('click', this.handleClick);
      this.host.removeEventListener('focus', this.handleShow);
      this.host.addEventListener('focus', this.handleShow);
      this.host.removeEventListener('focusout', this.handleHide);
      this.host.addEventListener('focusout', this.handleHide);
    }

    if (this.isFocused) {
      this._dropdownRef.setAttribute('data-show', '');
      this.updatePosition();
    } else {
      this._dropdownRef.removeAttribute('data-show');
    }
  }

  componentDidLoad() {
    const hasValidValue = (typeof this.value === 'string' && this.value.trim() !== '') || (Array.isArray(this.value) && this.value.length > 0);

    if (hasValidValue) {
      this.valueChanged.emit(this.value);
    }
  }

  disconnectedCallback() {
    if (this._cleanupAutoUpdate) {
      this._cleanupAutoUpdate();
      this._cleanupAutoUpdate = undefined;
    }
  }

  private async updatePosition() {
    if (!this._dropdownToggleRef || !this._dropdownMenuRef) return;

    const middleware: Middleware[] = [
      offset(3),
      flip({
        fallbackPlacements: ['top-start', 'bottom-start'],
      }),
      shift(),
      size({
        apply: ({ availableWidth, availableHeight, elements }) => {
          Object.assign(elements.floating.style, {
            maxWidth: `${availableWidth}px`,
            maxHeight: `${availableHeight}px`,
          });

          elements.floating.style.minWidth = `${elements.reference.getBoundingClientRect().width}px`;
        },
      }),
    ];

    const { x, y } = await computePosition(this._dropdownToggleRef, this._dropdownMenuRef, {
      placement: this.placement,
      middleware,
    });

    Object.assign(this._dropdownMenuRef.style, {
      left: `${x}px`,
      top: `${y}px`,
      position: 'fixed',
    });
  }

  private readonly handleClick = (ev: Event) => {
    ev.stopPropagation();
    if (this.isFocused) {
      this.host.blur();
      this.hideMenu.emit();
    } else {
      this.host.focus();
    }
  };

  private readonly handleHide = (ev: Event) => {
    ev.stopPropagation();
    const clickedElement = ev?.['relatedTarget'] as HTMLElement;
    if (!clickedElement || !this.host.contains(clickedElement)) {
      this.hideMenu.emit();
    }
  };

  private readonly handleShow = (ev: Event) => {
    ev.stopPropagation();
    this.showMenu.emit();
  };
}
